import React, { useState, useMemo } from 'react';
import { useNavigate } from 'react-router-dom';
import API from '../api';

function Register() {
  const [form, setForm] = useState({ username: '', password: '', confirmPassword: '' });
  const [error, setError] = useState('');
  const navigate = useNavigate();

  const handleChange = (e) => setForm(f => ({ ...f, [e.target.name]: e.target.value }));

  const complexityError = useMemo(() => {
    const pw = form.password;
    if (pw.length < 8) return 'Au moins 8 caractères';
    if (!/[A-Z]/.test(pw)) return 'Au moins une majuscule';
    if (!/[a-z]/.test(pw)) return 'Au moins une minuscule';
    if (!/[0-9]/.test(pw)) return 'Au moins un chiffre';
    if (!/[^A-Za-z0-9]/.test(pw)) return 'Au moins un caractère spécial';
    return '';
  }, [form.password]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');
    if (complexityError) return setError(complexityError);
    if (form.password !== form.confirmPassword) return setError('Les mots de passe ne correspondent pas');
    try {
      await API.post('/auth/register', {
        username: form.username.trim(),
        password: form.password.trim(),
        confirmPassword: form.confirmPassword.trim(),
      });
      navigate('/'); // retour login
    } catch (err) {
      const msg = err.response?.data?.error || err.response?.data?.message || 'Erreur';
      setError(msg);
    }
  };

  return (
    <form onSubmit={handleSubmit}>
      <h2>Inscription</h2>
      {error && <div style={{ color: 'red', marginBottom: '1em' }}>{error}</div>}
      <input
        name="username"
        placeholder="Nom d'utilisateur"
        value={form.username}
        onChange={handleChange}
        required
      />
      <input
        name="password"
        type="password"
        placeholder="Mot de passe"
        value={form.password}
        onChange={handleChange}
        required
      />
      <input
        name="confirmPassword"
        type="password"
        placeholder="Confirmez le mot de passe"
        value={form.confirmPassword}
        onChange={handleChange}
        required
      />
      {!error && complexityError && (
        <div style={{ color: '#a66', marginTop: 6, fontSize: 12 }}>{complexityError}</div>
      )}
      <button type="submit" disabled={!!complexityError || !form.username || !form.password || !form.confirmPassword}>
        Créer un compte
      </button>
    </form>
  );
}

export default Register;
