import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import API from '../api';

function Login() {
  const [form, setForm] = useState({ username: '', password: '' });
  const [error, setError] = useState('');
  const navigate = useNavigate();

  const handleChange = (e) => {
    setForm(f => ({ ...f, [e.target.name]: e.target.value }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');
    try {
      const payload = {
        username: form.username.trim(),
        password: form.password.trim(),
      };
      const { data } = await API.post('/auth/login', payload);
      localStorage.setItem('token', data.token);
      localStorage.setItem('role', data.user?.role || '');
      navigate('/schedule');
    } catch (err) {
      const msg = err.response?.data?.error || err.response?.data?.message || `HTTP ${err.response?.status || 0}`;
      setError(msg);
    }
  };

  // Redirige si déjà connecté
  useEffect(() => {
    if (localStorage.getItem('token')) {
      navigate('/schedule', { replace: true });
    }
  }, [navigate]);

  return (
    <form onSubmit={handleSubmit}>
      <h2>Connexion</h2>
      {error && <div style={{ color: 'red', marginBottom: '1em' }}>{error}</div>}
      <input name="username" placeholder="Nom d'utilisateur" onChange={handleChange} required />
      <input name="password" type="password" placeholder="Mot de passe" onChange={handleChange} required />
      <button type="submit">Se connecter</button>
      <div style={{ marginTop: '0.5rem' }}>
        <a href="/register">Créer un compte</a>
      </div>
    </form>
  );
}

export default Login;
