import React from 'react';
import { NavLink, useNavigate } from 'react-router-dom';

export default function NavBar() {
  const token = localStorage.getItem('token');
  const role = localStorage.getItem('role');
  const navigate = useNavigate();

  const logout = () => {
    localStorage.clear();
    navigate('/login');
  };

  return (
    <nav style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', padding: 10, borderBottom: '1px solid var(--line)', marginBottom: 12, background: 'var(--panel)' }}>
      {/* Gauche: Home */}
      <div style={{ display: 'flex', alignItems: 'center', gap: 12 }}>
        <NavLink to="/" style={({ isActive }) => ({ ...styles.link, ...(isActive ? styles.activeLink : {}) })}>
          Home
        </NavLink>
        {token && (
          <button
            type="button"
            className="week-btn"
            style={{ padding: '4px 8px' }}
            onClick={() => {
              const ev = new Event('open-help');
              window.dispatchEvent(ev);
            }}
          >
            ? Aide
          </button>
        )}
      </div>

      {/* Droite: le reste du menu */}
      <div style={{ display: 'flex', alignItems: 'center', gap: 12 }}>
        {!token && (
          <>
            <NavLink to="/clubs" style={({ isActive }) => ({ ...styles.link, ...(isActive ? styles.activeLink : {}) })}>
              Clubs
            </NavLink>
            <NavLink to="/events" style={({ isActive }) => ({ ...styles.link, ...(isActive ? styles.activeLink : {}) })}>
              Événements
            </NavLink>
            <NavLink to="/login" style={({ isActive }) => ({ ...styles.link, ...(isActive ? styles.activeLink : {}) })}>
              Login
            </NavLink>
            <NavLink to="/register" style={({ isActive }) => ({ ...styles.link, ...(isActive ? styles.activeLink : {}) })}>
              Inscription
            </NavLink>
          </>
        )}
        {token && (
          <>
            <NavLink to="/schedule" style={({ isActive }) => ({ ...styles.link, ...(isActive ? styles.activeLink : {}) })}>
              Emploi du temps
            </NavLink>
            <NavLink to="/clubs" style={({ isActive }) => ({ ...styles.link, ...(isActive ? styles.activeLink : {}) })}>
              Clubs
            </NavLink>
            <NavLink to="/events" style={({ isActive }) => ({ ...styles.link, ...(isActive ? styles.activeLink : {}) })}>
              Événements
            </NavLink>
            <NavLink to="/chat" style={({ isActive }) => ({ ...styles.link, ...(isActive ? styles.activeLink : {}) })}>
              Chat
            </NavLink>
            <NavLink to="/account" style={({ isActive }) => ({ ...styles.link, ...(isActive ? styles.activeLink : {}) })}>
              Mon compte
            </NavLink>
            {role === 'admin' && (
              <NavLink to="/admin" style={({ isActive }) => ({ ...styles.link, ...(isActive ? styles.activeLink : {}) })}>
                Admin
              </NavLink>
            )}
            <button onClick={logout} style={styles.button}>Se déconnecter</button>
          </>
        )}
      </div>
    </nav>
  );
}

const styles = {
  link: { color: 'var(--text)', textDecoration: 'none', fontWeight: 'bold' },
  activeLink: { textDecoration: 'underline' },
  button: {
    backgroundColor: 'crimson',
    color: 'white',
    border: 'none',
    padding: '6px 10px',
    cursor: 'pointer',
    borderRadius: '4px',
  },
};
