// src/api.js
import axios from 'axios';

// Derive API base URL:
// - REACT_APP_API_URL wins if set (e.g., http://192.168.1.10:5000)
// - else use current host with port 5000 (works on LAN when opening client via http://<PC_IP>:3000)
const DEFAULT_API_BASE =
  (typeof window !== 'undefined')
    ? `${window.location.protocol}//${window.location.hostname}:5000`
    : 'http://localhost:5000';
const API_BASE = process.env.REACT_APP_API_URL || DEFAULT_API_BASE;

const API = axios.create({
  baseURL: API_BASE,
});

// Ajouter automatiquement le token si dispo
API.interceptors.request.use((req) => {
  const token = localStorage.getItem('token');
  if (token) req.headers.Authorization = `Bearer ${token}`;
  return req;
});

// Déconnexion auto sur 401
API.interceptors.response.use(
  (res) => res,
  (error) => {
    const status = error?.response?.status;
    if (status === 401) {
      localStorage.removeItem('token');
      localStorage.removeItem('role');
      if (window.location.pathname !== '/login') {
        window.location.assign('/login');
      }
    }
    return Promise.reject(error);
  }
);

export default API;
