import { Routes, Route } from 'react-router-dom';
import React, { Suspense, lazy, useEffect, useState } from 'react'; // consolidated React import
import Login from './components/Login';
import Register from './components/Register';
import ProtectedRoute from './components/ProtectedRoute';
import NavBar from './components/NavBar';
import './App.css';
import Home from './components/Home';
import HelpModal from './components/HelpModal';

// Route-level code splitting
const Schedule = lazy(() => import('./components/Schedule'));
const AccountPanel = lazy(() => import('./components/AccountPanel'));
const AdminPanel = lazy(() => import('./components/AdminPanel'));
const Clubs = lazy(() => import('./components/Clubs'));
const Events = lazy(() => import('./components/Events'));
const Games = lazy(() => import('./components/Games'));
const GameTicTacToe = lazy(() => import('./components/games/GameTicTacToe'));
const GameFlappyBird = lazy(() => import('./components/games/GameFlappyBird'));
const GameTicTacToeOnline = lazy(() => import('./components/games/GameTicTacToeOnline'));
const Mmorpg = lazy(() => import('./components/games/mmorpg/Mmorpg'));
const WebGame = lazy(() => import('./components/games/WebGame'));
const CardBattle = lazy(() => import('./components/games/CardBattle'));
const Chat = lazy(() => import('./components/Chat'));

function App() {
  const [showHelp, setShowHelp] = useState(false);

  // Ouvre l’aide si première visite après authentification
  useEffect(() => {
    const token = localStorage.getItem('token');
    if (token && !localStorage.getItem('seenHelpV1')) {
      setShowHelp(true);
      localStorage.setItem('seenHelpV1', '1');
    }
  }, []);

  // Listener bouton NavBar (événement custom)
  useEffect(() => {
    const handler = () => setShowHelp(true);
    window.addEventListener('open-help', handler);
    return () => window.removeEventListener('open-help', handler);
  }, []);

  return (
    <>
      <NavBar />
      <div style={{ padding: 12 }}>
        <Suspense fallback={<div>Chargement…</div>}>
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/login" element={<Login />} />
          <Route path="/register" element={<Register />} />
          <Route path="/schedule" element={<ProtectedRoute><Schedule /></ProtectedRoute>} />
          <Route path="/clubs" element={<Clubs />} />
          <Route path="/clubs/:id" element={<Clubs />} />
          <Route path="/events" element={<Events />} />
          <Route path="/admin" element={<ProtectedRoute requiredRole="admin"><AdminPanel /></ProtectedRoute>} />
          <Route path="/account" element={<ProtectedRoute><AccountPanel /></ProtectedRoute>} />
          <Route path="/games" element={<Games />} />
          <Route path="/games/morpion" element={<GameTicTacToe />} />
          <Route path="/games/morpion-online" element={<GameTicTacToeOnline />} />
          <Route path="/games/flappybird" element={<GameFlappyBird />} />
          <Route path="/games/mmorpg" element={<Mmorpg />} />
          <Route path="/games/web/:slug" element={<WebGame />} />
          <Route path="/games/card-battle" element={<CardBattle />} />
          <Route path="/chat" element={<ProtectedRoute><Chat /></ProtectedRoute>} />
        </Routes>
        </Suspense>
      </div>
  <HelpModal open={showHelp} onClose={() => setShowHelp(false)} />
    </>
  );
}

export default App;
